import React from 'react'
import Link from 'gatsby-link'
import PropTypes from 'prop-types'
import injectSheet from 'react-jss'
import LazyLoad from 'react-lazyload'

const styles = theme => ({
  listItem: {
    margin: '0 0 .7em 0',
    transition: 'height 1s',
    [`@media (min-width: ${theme.mediaQueryTresholds.M}px)`]: {
      margin: '0 0 1.5rem 0',
    },
    [`@media (min-width: ${theme.mediaQueryTresholds.L}px)`]: {
      '.moving-featured &, .is-aside &': {
        margin: '0 0 0 0',
      },
    },
  },
  listLink: {
    display: 'flex',
    alignContent: 'center',
    alignItems: 'center',
    justifyContent: 'flex-start',
    flexDirection: 'row',
    padding: '.7em 1em .7em 1em',
    color: theme.navigator.colors.postsListItemLink,
    '@media (hover: hover)': {
      '&:hover': {
        color: theme.navigator.colors.postsListItemLinkHover,
        '& .pointer': {
          borderRadius: '65% 75%',
        },
      },
    },
  },
  listItemPointer: {
    position: 'relative',
    flexShrink: 0,
    overflow: 'hidden',
    backgroundColor: theme.navigator.colors.postsListItemPointerBackground,
    borderRadius: '75% 65%',
    width: '60px',
    height: '60px',
    margin: '0',
    transition: 'all .5s',
    '& img': {
      width: '100%',
      height: '100%',
    },
    [`@media (min-width: ${theme.mediaQueryTresholds.M}px)`]: {
      marginRight: '.5em',
      width: '80px',
      height: '80px',
    },
    [`@media (min-width: ${theme.mediaQueryTresholds.L}px)`]: {
      marginRight: '.8em',
      width: '90px',
      height: '90px',
      transition: 'all .3s',
      transitionTimingFunction: 'ease',
      '.moving-featured &, .is-aside &': {
        width: '30px',
        height: '30px',
      },
    },
  },
  listItemText: {
    margin: '0 0 0 1.5em',
    flexGrow: 1,
    display: 'flex',
    flexDirection: 'column',
    width: '100%',
    '& h1': {
      lineHeight: 1.15,
      fontWeight: 600,
      letterSpacing: '-0.03em',
      margin: 0,
      fontSize: `${theme.navigator.sizes.postsListItemH1Font}em`,
      [`@media (min-width: ${theme.mediaQueryTresholds.M}px)`]: {
        fontSize: `${theme.navigator.sizes.postsListItemH1Font *
          theme.navigator.sizes.fontIncraseForM}em`,
      },
      [`@media (min-width: ${theme.mediaQueryTresholds.L}px)`]: {
        fontSize: `${theme.navigator.sizes.postsListItemH1Font *
          theme.navigator.sizes.fontIncraseForL}em`,
        '.moving-featured &, .is-aside &': {
          fontSize: '1em',
          fontWeight: 400,
        },
      },
    },
    '& h2': {
      lineHeight: 1.2,
      display: 'block',
      fontSize: `${theme.navigator.sizes.postsListItemH2Font}em`,
      margin: '.3em 0 0 0',
      [`@media (min-width: ${theme.mediaQueryTresholds.M}px)`]: {
        fontSize: `${theme.navigator.sizes.postsListItemH2Font *
          theme.navigator.sizes.fontIncraseForM}em`,
      },
      [`@media (min-width: ${theme.mediaQueryTresholds.L}px)`]: {
        fontSize: `${theme.navigator.sizes.postsListItemH2Font *
          theme.navigator.sizes.fontIncraseForL}em`,
        '.moving-featured &, .is-aside &': {
          display: 'none',
        },
      },
    },
    [`@media (min-width: ${theme.mediaQueryTresholds.L}px)`]: {
      '.moving-featured &, .is-aside &': {
        margin: '0 0 0 .5em',
      },
    },
  },
})

class ListItem extends React.Component {
  state = {
    hidden: false,
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.categoryFilter !== this.props.categoryFilter) {
      const category = this.props.post.node.frontmatter.category
      const categoryFilter = this.props.categoryFilter

      if (categoryFilter === 'all posts') {
        this.setState({hidden: false})
      } else if (category !== categoryFilter) {
        this.setState({hidden: true})
      } else if (category === categoryFilter) {
        this.setState({hidden: false})
      }
    }
  }

  render() {
    const {
      classes,
      post: {node: {fields: {slug}, frontmatter: {cover, category, title, subTitle}}},
      linkOnClick,
    } = this.props
    const {hidden} = this.state
    const style = {
      display: hidden ? 'none' : 'block',
    }
    let pointerImage

    if (cover) {
      pointerImage = (
        <LazyLoad height={60} overflow={true} throttle={300} once={true} offset={100}>
          <picture>
            <source type="image/webp" srcSet={cover.children[0].resolutions.srcSetWebp} />
            <source srcSet={cover.children[0].resolutions.srcSet} />
            <img src={cover.children[0].resolutions.src} alt="" />
          </picture>
        </LazyLoad>
      )
    }

    return (
      <li className={`${classes.listItem} ${category}`} style={style} key={slug}>
        <Link activeClassName="active" className={classes.listLink} to={slug} onClick={linkOnClick}>
          <div className={`${classes.listItemPointer} pointer`}>{pointerImage}</div>
          <div className={classes.listItemText}>
            <h1>{title}</h1>
            {subTitle && <h2>{subTitle}</h2>}
          </div>
        </Link>
      </li>
    )
  }
}

ListItem.propTypes = {
  classes: PropTypes.object.isRequired,
  post: PropTypes.object.isRequired,
  linkOnClick: PropTypes.func.isRequired,
  categoryFilter: PropTypes.string.isRequired,
}

export default injectSheet(styles)(ListItem)



